// out: false

/* Global */
:root {
	--main-background-color: white;
	--secondary-background-color: linear-gradient(hsl(210, 83%, 70%), hsl(195, 83%, 75%));
	--main-text-color: black;
	--main-outline-color: rgba(0, 0, 0, .15);
	--dense-outline-color: rgba(0, 0, 0, .5);
}
html.dark-mode {
	--main-background-color: black;
	--secondary-background-color: linear-gradient(hsl(0, 90%, 40%), hsl(15, 90%, 40%));
	--main-text-color: white;
	--main-outline-color: rgba(255, 255, 255, .5);
	--dense-outline-color: rgba(255, 255, 255, .8);
	background: black; /* When resizing */
}
h3, p, li {
	font-family: 'Raleway', 'Comic Sans MS', serif;
	color: var(--main-text-color);
	font-weight: 300;
}
.flexrow {
	display: flex;
	flex-flow: row;
}
.flexcenter {
	justify-content: center;
}
.flexend {
	justify-content: flex-end;
}
.round-btn {
	padding: 5px;
	background: var(--main-background-color);
	border-radius: 50px;
	outline: solid 1px var(--main-outline-color);

	&:hover {
		outline: solid 1px var(--dense-outline-color);
	}
}

/* Specific */
html,
body {
	height: 100%;
	width: 100%;
	margin: 0;
	position: relative;
	overflow-x: hidden;
	overflow-y: hidden;
}
#board {
	position: relative;
	height: 100vh;
}
canvas#front-layer {
	position: absolute;
	top: 0;
	left: 0;
}
#nodes-container {
	position: absolute;
	top: 0;
	left: 0;

	p {
		position: absolute;
		text-align: center;
		margin: 0;
		user-select: none;
		word-wrap: auto;
		opacity: .15;
		text-transform: uppercase;
		
		&.active {
			cursor: pointer;
			opacity: 1;
			
			&:hover {
				text-shadow: var(--main-text-color) 0 0 1px;
			}
		}
		&:not(.active) { }
	}
}
.filter,
.page-link {
	position: relative;
	transition: border-color 200ms ease-in;
	border-bottom: solid 2px transparent;
	border-top: solid 2px transparent;

	h3 {
		font-size: 1rem;
		text-transform: uppercase;
		font-weight: 500;
		border-radius: 9999px;
		border: solid 1px var(--main-text-color);
		padding: .25rem 1rem;
		user-select: none;
	}
}
#search {
	position: absolute;
	margin: 0 auto;
	bottom: 1rem;
	left: 1rem;

	#filters {
		display: flex;
		gap: 1rem;
		flex-flow: row;
		justify-content: center;
	} 

	.filter {
		
		&.active {
			border-color: var(--main-text-color);
		}
		
		ul.filter-tags {
			list-style-type: none;
			position: absolute;
			bottom: 15px;
			width: 165px;
			padding: 0 0 5px 0;
			display: none;
			flex-flow: row;
			flex-wrap: wrap;
			justify-content: center;
			overflow-x: hidden !important;
			
			li {
				text-align: center;
				cursor: pointer;
				user-select: none;
				width: 150px;
				margin: 3px;
				font-size: .85em;
				
				&:hover {
					outline: var(--dense-outline-color) solid 1px;
				}
				&.active {
					background: var(--main-text-color);
					color: var(--main-background-color);
				}
			}
		}
		&:hover ul.filter-tags {
			display: flex;
		}
		
		&#filter-labo ul.filter-tags {
			left: -32px;
		}
		&#filter-category ul.filter-tags {
			left: -37px;
		}
		&#filter-author ul.filter-tags {
			left: -122px;
			width: 335px;
			bottom: 17px;
		
		
			li {
				margin: 3px;
				order: 2;
				
				&:last-of-type:nth-child(odd) {
					order: 1;
					margin: 3px 10px;
				}
			}
		}
	}

	#expand-search {
		display: none;
	}
}
#search-info {
position: absolute;
top: 0;
left: 0;
width: 100%;
user-select: none;

p {
	font-size: 2rem;
	line-height: 1em;
	text-align: center;
	font-weight: 200;
	padding: 0 1em;
}
}
#pages {
	position: absolute;
	margin: 0 auto;
	bottom: 1rem;
	right: 1rem;
	display: flex;
	gap: 1rem;
	flex-flow: row;
	justify-content: center;

	.page-link {
		
	}
}
#dark-mode-toggle {
position: absolute;
height: 35px;
width: 35px;
border-radius: 50%;
bottom: 4rem;
right: 1rem;
cursor: pointer;
background: var(--main-text-color);
border: solid 1px rgba(0, 0, 0, .2);
z-index: 11;

&:hover {
	box-shadow: var(--main-text-color) 0 0 6px;
}
}

/* Mobile */
@media (max-width: 550px) {
html, body {
	overflow-y: inherit;
}
#dark-mode-toggle {
	top: 10px;
	right: 10px;
}
#mobile-search {
	display: inherit;
}
#search {
	bottom: calc(100% - 35px);
	left: inherit;
	background: var(--secondary-background-color);
	height: auto;
	border-radius: 0;
	width: 100%;
	outline: none;
	padding: 0;
	box-shadow: rgba(0, 0, 0, .5) 0 0 6px;
	
	&:not(.active) .filter {
	opacity: 1;
	}
	
	#filters {
	flex-flow: column;
	}
	
	.filter {
	padding: 0 3px;
	opacity: .6;
	
	&.active {
		border-color: transparent;
		opacity: 1;
	}
	
	h3 {
		text-align: center;
		padding: 0.2rem;
		font-size: 1.2rem;
	}
	&#filter-labo ul.filter-tags,
	&#filter-category ul.filter-tags,
	&#filter-author ul.filter-tags {
		left: inherit;
		width: inherit;
	}
	&:hover ul.filter-tags {
		display: flex;
	}
	ul.filter-tags {
		display: flex;
		flex-flow: row;
		flex-wrap: wrap;
		justify-content: center;
		position: relative;
		width: 100%;
		overflow-x: scroll;
		bottom: inherit;
		left: inherit;
		margin: 0;
		
		li {
		width: 90px;
		margin: 3px;
		padding: 2px 3px;
		font-size: .8rem;
		display: flex;
		flex-flow: row;
		align-items: center;
		justify-content: center;
		}
	}
	&#filter-author ul.filter-tags {
		bottom: inherit;
		
		li:last-of-type:nth-child(odd) {
		margin: 3px;
		}
	}
	}
	
	#expand-search {
	display: flex;
	flex-flow: rox;
	justify-content: center;
	align-items: center;
	background: rgba(0, 0, 0, .06);
	height: 35px;
	cursor: pointer;
	
	#square {
		width: 12px;
		height: 12px;
		margin-top: -9px;
		transform: rotate(225deg);
		border-top: solid 1px rgba(0, 0, 0, .7);
		border-left: solid 1px rgba(0, 0, 0, .7);
		transition: transform 200ms ease-out;
	}
	&:hover #square {
		border-color: black;
	}
	}
	
	&:not(.expanded) {
	bottom: inherit;
	
	#expand-search #square {
		margin-top: 7px;
		transform: rotate(45deg);
	}
	}
}
#board {
	margin-top: 35px;
}
#search-info p {
	padding: 0 2.8rem;
	font-size: 1.7rem;
}
}
